import React from "react";
import { useState } from "react";
import { getMortyById, getMortyByNameFilter, getMortyByStatusFilter, getMortyBySpeciesFilter, getHP } from "../service";

import { Button, Grid, TextField } from "@mui/material";
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

let id = 0;
let Name = 'name', Species = 'species', Status = 'status';

const SearchCharacter = (props) => {
    const { setDataList, datalist } = props;
    const[hpORmorty,setHpOrMorty]=useState(false);
    const [idstate, setIdState] = useState([
        {
            id: ''
        }]
    );

    const [name, setName] = React.useState('');
    const [filtertext, setFilterText] = useState('');

    const handleChange = (event) => {
        setName(event.target.value);
        console.log(name);
    };

    const idHandler = (event) => {
        setIdState({
            ...idstate,
            id: event.target.value
        });
        console.log(idstate);
    }

    const filterHandler = (event) => {
        setFilterText(event.target.value);
        console.log(filtertext);
    }

    const filterData = () => {
        if (name === 'name') {
            console.log('Filtering based on name.....')
            getMortyByNameFilter({ setDataList, filtertext });
            console.log(datalist);
        }
        if (name === 'species') {
            getMortyBySpeciesFilter({ setDataList, filtertext });
            console.log('Filtering based on species.....');
        }
        if (name === 'status') {
            console.log('Filtering based on status.....')
            getMortyByStatusFilter({ setDataList, filtertext });
        }
    }

    const submitAction = (event) => {
        event.preventDefault();
        id = idstate.id;
        getMortyById({ id, setDataList });
    }

    const ShowHpChars = () => {
        setHpOrMorty(!hpORmorty)
        if(hpORmorty===true)
        {
            window.location.reload();
            
        }
        else
        {
            getHP({setDataList});
        }
        
        console.log("Showing Harry Potter Characters...");
    }

    const Reset = () => {
        window.location.reload();
    }

    return (
        <Grid container spacing={1} justifyContent='center' alignContent='center' alignItems='center' style={{marginTop:20}}>
            <form onSubmit={submitAction}>
                <Grid container spacing={2} justifyContent="center" alignContent="center" alignItems="center">
                    <Grid item>
                        <TextField color='secondary' placeholder="enter id" onChange={idHandler} />
                    </Grid>
                    <Grid item>
                        <Grid container spacing={6} justifyContent="center" alignContent="center" alignItems="center">
                            <Grid item>
                                <Button variant="contained" type="submit"  >SEARCH</Button>
                            </Grid>
                            <Grid item>
                                <Button variant="contained" onClick={Reset}>RESET</Button>
                            </Grid>
                            <Grid item>
                                <Button onClick={ShowHpChars}>Toggle Characters</Button>
                            </Grid>
                            <Grid item>
                                <Box sx={{ minWidth: 120 }}>
                                    <FormControl fullWidth>
                                        <InputLabel id="demo-simple-select-label">Filter</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            value={name}
                                            label="Age"
                                            onChange={handleChange}>
                                            <MenuItem value={Name}>Name</MenuItem>
                                            <MenuItem value={Status}>Status</MenuItem>
                                            <MenuItem value={Species}>Species</MenuItem>
                                        </Select>
                                    </FormControl>
                                </Box>
                            </Grid>
                            <Grid item>
                                <TextField placeholder="enter" onChange={filterHandler}></TextField>
                            </Grid>
                            <Grid item>
                                <Button variant="contained" color="secondary" onClick={filterData}>FILTER</Button>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </form>
        </Grid>
    )
}

export default SearchCharacter;
