import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import { Grid } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import LogoutIcon from '@mui/icons-material/Logout';
import Typewriter from "./Typewriter";
import Switch from '@mui/material/Switch';

export default function Appbar(props) {
  const navigate = useNavigate();
  const { themeboolean, setThemeState,themestate } = props;
  const Logout = () => {
    navigate('/');
  };


  const switchTheme = () => {
    props.setThemeBoolean(!themeboolean);
    if (themeboolean === true) {
      setThemeState('dark');
    }
    else {
      setThemeState('light');
    }
  }

  return (
    <AppBar position="static" sx={{ height: 80 }}>
      <Toolbar>
        <Grid container alignItems="center" justifyContent="space-between">
          <Grid item>
            <IconButton size="large" edge="start" color="inherit" aria-label="menu">
              <MenuIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <Typography variant="h3" component="div">
              <Typewriter text="Rick And Morty...." delay={300} infinite />
            </Typography>
          </Grid>
          <Grid item>
            <Grid container spacing={6} alignItems="center" justifyContent='center' alignContent='center'>
              <Grid item>
                <Grid container justifyContent='center' alignContent='center' alignItems='center'>
                  <Grid item>
                  <Switch
                   onClick={switchTheme}
                  inputProps={{ 'aria-label': 'controlled' }}
                ></Switch>
                  </Grid>
                  <Grid item>
                  <Typography>{themestate.toUpperCase()} Mode</Typography>
                  </Grid>
                </Grid>
              
               
              </Grid>
              <Grid item>
                <LogoutIcon onClick={Logout}></LogoutIcon>
                
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>
  );
}
