import React, { useEffect, useState } from 'react';

import { Grid, ThemeProvider, createTheme } from '@mui/material';

import Characters from './Characters';
import SearchCharacter from './SearchCharacter';
import '../App.css'
import { getMorty } from '../service';
import Appbar from './Appbar';
// import Sidebar from './Sidebar';
import Footer from './Footer';
import Paginator from './Paginator';
import { blue } from '@mui/material/colors';


function App() {


  const[themestate,setThemeState]=useState('dark');
  const[themeboolean,setThemeBoolean]=useState(false);

  const theme = createTheme({
    palette: {
      mode: themestate,
      text: {
        primary:themeboolean?"#ffffff": blue[200]
      }
    }

    
  });

  const [idstate, setIdState] = useState();
  const[count,setCount]=useState();
  const [datalist, setDataList] = useState([
    {
      id: '',
      name: '',
      status: '',
      species: '',
      image: '',
      gender:''
    }]
  )


  useEffect(() => {
    
    getMorty({ datalist, setDataList,count,setCount });
  },// eslint-disable-next-line
   [])
  

  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        
        {/* <Sidebar /> */}
        <Appbar setThemeState={setThemeState} setThemeBoolean={setThemeBoolean} themestate={themestate} themeboolean={themeboolean} />
         <Grid container justifyContent='center' style={{ marginTop: 20 }}>
          <Grid item xs={12}>
            <Grid container justifyContent='center' alignItems='center' alignContent='center' >
              <Grid item xs={12}>
                <SearchCharacter idstate={idstate} setIdState={setIdState} setDataList={setDataList} datalist={datalist} />
              </Grid>
            </Grid>
          </Grid>
          <Grid container justifyContent='center' alignContent='center' alignItems='center' style={{ marginTop: 20, margin: 20 }} className="card_container">
            <Grid item xs={12}>
              <Characters datalist={datalist} setCount={setCount} count={count} setDataList={setDataList}/>
            </Grid>
          </Grid>
          <Grid item>
          <Grid container justifyContent='center' alignContent='center' alignItems='center'>
            <Grid item xs={12}>
              <Paginator setDataList={setDataList} />
            </Grid>
          </Grid>
          </Grid>
         

        </Grid>
       
        <Footer />
      </ThemeProvider>
    </div>

  );
}

export default App;
