// import Card from "./Card";
// import Character from "./Character";
import { Avatar, Grid } from "@mui/material";
import MUIDataTable from "mui-datatables";
import { getMortyByPage, getMortyByNameFilter, getMortyByStatusFilter, getMortyBySpeciesFilter, getMortyByGenderFilter } from "../service";
import { useState } from "react";
import '../App.css'




const Characters = (props) => {

    // const [status, setStatus] = useState('');
    // const [species, setSpecies] = useState('');
    // const [gender, setGender] = useState('');
    const [searchText, setSearchText] = useState('');
    const { count, setCount, setDataList } = props;
   
    const columns = [
        {
            name: "id",
            label: "id",
            options: {
                filter: false,
                sort: true,
            }
        },
        {

            name: "name",
            label: "Name",
            options: {
                filter: false,
                sort: true,
                
                
            }
        },
        {
            name: "image",
            label: "image",
            options: {
                filter: false,
                sort: true,
                customBodyRender: (value) => <Avatar src={value} alt="Image" />,

            }
        },
        {
            name: "gender",
            label: "gender",
            options: {
                filter: true,
                sort: true,
              
                
            }
        },
        {
            name: "status",
            label: "status",
            options: {
                filter: true,
                sort: true,
                
            }
        },
        {
            name: "species",
            label: "species",
            options: {
                filter: true,
                sort: false,
            }
        },

    ];


    const options = {
        serverSide: true,
        filter: true,
        rowsPerPage: 20,
        confirmFilters: false,
        rowsPerPageOptions: [10, 20, 30, 50],
        count: count,
        onTableChange: (action, tableState) => {
            if (action === "changePage") {
                const nextPage = tableState.page + 1;
                getMortyByPage({ setDataList, page: nextPage });
            }
        },
        onFilterChange: (changedColumn, filterList) => {
            console.log("Filter changed...", changedColumn, filterList);
            console.log(filterList);
            if (filterList.length === 0) {
                // getMortyByPage({ setDataList, page: 1,setCount });
                // if (changedColumn === "name") {
                //     getMortyByNameFilter({ setDataList, filtertext: "" });
                // } else if (changedColumn === "species") {
                //     getMortyBySpeciesFilter({ setDataList, filtertext: "" });
                // } else if (changedColumn === "status") {
                //     getMortyByStatusFilter({ setDataList, filtertext: "" });
                // }
            } else {
                // Apply the filter based on the selected value
                if (changedColumn === "name") {
                    const filtertext = filterList[1][0];

                    getMortyByNameFilter({ setDataList, filtertext,setCount });
                } else if (changedColumn === "species") {
                    const filtertext = filterList[5][0];
                    getMortyBySpeciesFilter({ setDataList, filtertext,setCount });
                } else if (changedColumn === "status") {
                    const filtertext = filterList[4][0];
                    console.log(searchText);
                    getMortyByStatusFilter({ setDataList, filtertext,setCount });
                }
                else if (changedColumn === "gender") {
                    const filtertext = filterList[3][0];
                    getMortyByGenderFilter({ setDataList, filtertext,setCount });
                }
            }
        },
        onSearchChange: (searchText) => {
            setSearchText(searchText);
            if (searchText) {
                // Call the appropriate API based on the column
                const filtertext = searchText.trim(); // Trim the search text
                if (columns[1].options.filter) {
                    console.log(columns[1].options.filter);
                    getMortyByNameFilter({ setDataList, filtertext });
                    console.log(columns[1].options.filter);
                } else if (columns[4].options.filter) {
                    console.log(columns[4].options.filter);
                    getMortyByStatusFilter({ setDataList, filtertext });
                } else if (columns[5].options.filter) {
                    getMortyBySpeciesFilter({ setDataList, filtertext });
                }
            } else {
                // No search text, fetch data without filters
                getMortyByPage({ setDataList, page: 1 });
            }
        },
        onreset:()=>{
            window.location.reload();
        }

    };


    return (
        <Grid container justifyContent='center' className="translucent-table">
            <Grid item xs={12}>
                <MUIDataTable
                 
                  
                    title={<span style={{ color:  'dark' ? '#000000' : '#ffffff' }}>Characters</span>}
                    data={props.datalist}
                    columns={columns}
                    options={options}
                />

            </Grid>


        </Grid>


    )



}


export default Characters;