export const getMorty = async (props) => {
  const { setDataList,setCount } = props
  try {
    const mortydata = await fetch('https://rickandmortyapi.com/api/character');
    const data = await mortydata.json();
    setDataList(data.results);
    setCount(data.info.count)
    console.log(data.info.count);
    return data.results;
  } catch (error) {
    console.log('Api ', error.message);
  }

};








export const getMortyById = async (props) => {
  const { setDataList, id} = props

  try {
    const mortydata = await fetch(`https://rickandmortyapi.com/api/character/${id}`);
    // const mortydata = await fetch('https://rickandmortyapi.com/api/character/2');
    const data = await mortydata.json();
    //console.log('getbyid returned ',data);
    setDataList([data]);
    return data.results;
  } catch (error) {
    console.log('Api Get By Id Error ', error.message);
  }

};

